.customer-reviesw {
  padding-top: 60px;
  overflow-x: hidden;
 
  @include desktop {
    padding-top: 120px;
  }
}

.customer-reviesw .container {
  @include tablet {
    padding-left: 74px;
    padding-right: 74px;
  }

  @include desktop {
    padding-left: 116px;
    padding-right: 116px;
  }
}

.customer-reviesw__list {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}

.customer-reviesw__item {
  display: flex;
  flex-direction: column;
}

.customer-reviesw__img {
  margin: 0 auto;
}

.customer-reviesw__text {
  position: relative;
  width: 100%;
  margin-top: 32px;
  background-color: #fafafa;
  @include font(12px, 500, 2.5833, 0);
  text-align: center;
  padding-top: 43px;
  padding-right: 58px;
  padding-bottom: 26px;
  padding-left: 65px;
  flex-grow: 1;
  z-index: 1;
  background-image: repeating-linear-gradient(
    to bottom,
    #e1e1e1,
    #e1e1e1 1px,
    #fafafa 1px,
    #fafafa 31px
  );
  background-repeat: no-repeat;
  background-position: 50px 74px;
  background-size: calc(100% - 100px) 100%;

  @include tablet {
    margin-top: 20px;
    @include font(14px, 500, 2.16, 0.04em);
    padding-top: 37px;
    padding-right: 55px;
    padding-bottom: 44px;
    padding-left: 51px;
    background-image: repeating-linear-gradient(
      to bottom,
      #e1e1e1,
      #e1e1e1 1px,
      #fafafa 1px,
      #fafafa 30px
    );
    background-repeat: no-repeat;
    background-position: 6px 68px;
    background-size: calc(100% - 12px) calc(100% - 104px);
  }

  @include desktop {
    margin-top: 16px;
    @include font(16px, 500, 1.86, 0.04em);
    padding-top: 47px;
    padding-right: 109px;
    padding-bottom: 39px;
    padding-left: 109px;
    background-image: repeating-linear-gradient(
      to bottom,
      #e1e1e1,
      #e1e1e1 1px,
      #fafafa 1px,
      #fafafa 31px
    );
    background-repeat: no-repeat;
    background-position: 73px 76px;
    background-size: calc(100% - 147px) calc(100% - 107px);
  }

  &:before {
    position: absolute;
    content: '';
    background-image: url(/src/images/quotation_marks.svg);
    width: 54px;
    height: 40px;
    left: 53px;
    top: 30px;
    z-index: -1;
    @include tablet {
      width: 60px;
      left: 6px;
      top: 20px;
      background-repeat: no-repeat;
      background-size: cover;
    }
    @include desktop {
      width: 54px;
      left: 72px;
      top: 32px;
    }
  }
}

.customer-reviesw__dots {
  position: relative;
  display: block;
  margin: 0 auto;
  background-color: #c7133e;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  margin-top: 16px;
  margin-bottom: 16px;

  @include tablet {
    margin-top: 4px;
  }

  @include desktop {
    margin-top: 16px;
  }

  &::after {
    position: absolute;
    content: '';
    background-color: #c7133e;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    left: 14px;
  }

  &::before {
    position: absolute;
    content: '';
    background-color: #c7133e;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    right: 14px;
  }
}

.customer-reviesw__name {
  @include font(14px, 500, 1.86, 0);
  text-align: center;
  margin-bottom: 2px;

  @include tablet {
    @include font(16px, 500, 1.86, 0);
    margin-bottom: 18px;
  }
  @include desktop {
    @include font(20px, 500, 1.86, 0);
  }
}

.slick-dots li button {
  padding: 4;

  &:before {
    font-size: 14px;
    line-height: 24px;
    transition: color 250ms var(--typical-time-function);
  }

  &:hover:before {
    color: var(--pimary-accent-color);
  }
}

.slick-dots li.slick-active button {
  position: relative;

  &:before {
    position: absolute;
    content: '';
    width: 12px;
    height: 12px;
    background-image: url(/src/images/home_icon.svg);
    opacity: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
