*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

:root {
  --pimary-accent-color: #d41443;
  --secondary-accent-color: #ffa5ba;
  --primary-bg-color: #fff;
  --secondary-bg-color: #fff5f6;
  --hero-bg-color: #ffb8ca;
  --card1-bg-color: #ffb8ca;
  --card2-bg-color: #f0d1a5;
  --card3-bg-color: #c2e297;
  --mobile-menu-bg-color: #e17992;
  --btn-hover-bg: #ecebeb;
  --primary-text-color: #000;
  --secondary-text-color: #907e82;
  --tertiary-text-color: #fff;
  --accent-font: "Titan One", cursive;
  --main-font: "DM Sans", sans-serif;
  --typical-time-function: cubic-bezier(.4, 0, .2, 1);
}

body {
  background-color: var(--primary-bg-color);
  color: var(--primary-text-color);
  font-family: var(--main-font);
  font-size: 16px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.title {
  font-family: var(--accent-font);
  letter-spacing: .04em;
  text-align: center;
  text-transform: uppercase;
  color: var(--pimary-accent-color);
  font-size: 34px;
  font-weight: 400;
  line-height: 1.15;
}

@media screen and (min-width: 768px) {
  .title {
    font-size: 48px;
    line-height: 1.14;
  }
}

@media screen and (min-width: 1200px) {
  .title {
    letter-spacing: .06em;
    font-size: 58px;
    line-height: 1.14;
  }
}

.label {
  font-family: var(--accent-font);
  text-align: center;
  text-transform: uppercase;
  color: var(--secondary-accent-color);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.16;
}

@media screen and (min-width: 768px) {
  .label {
    font-size: 26px;
    font-weight: 400;
    line-height: 1.15;
  }
}

@media screen and (min-width: 1200px) {
  .label {
    letter-spacing: .04em;
    font-size: 30px;
    font-weight: 400;
    line-height: 1.13;
  }
}

.btn {
  text-align: center;
  border: none;
  border-radius: 22px;
  align-items: center;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

.btn--white {
  background-color: var(--primary-bg-color);
  color: var(--pimary-accent-color);
}

.btn--red {
